.advantage-card {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-top: 40px;
  padding-right: 10px;
  padding-bottom: 40px;
  padding-left: 42px;
  cursor: pointer;

  &:hover {
    background-color: #00b2ff;

    h3,
    p {
      color: #ffffff;
    }

    .advantage-card__icon {
      fill: #ffffff;
    }
  }
}

.advantage-card__icon {
  margin-bottom: 18px;
  fill: #00b2ff;
  order: -1;
}

.advantage-card h3 {
  margin: 0;
  margin-bottom: 16px;
  font-family: $second-font;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
  color: #00b2ff;
}

.advantage-card p {
  margin: 0;
  max-width: 482px;
}

@media (max-width: $tablet-width) {
  .advantage-card {
    min-height: 232px;
    padding-top: 24px;
    padding-right: 20px;
    padding-left: 25px;
  }

  .advantage-card h3 {
    margin-bottom: 10px;
  }

  .advantage-card__icon {
    margin-bottom: 6px;

    // &--hired,
    // &--tag {
    //   width: 32px;
    //   height: 32px;
    // }

    // &--detective {
    //   margin-bottom: 10px;
    //   width: 30px;
    //   height: 26px;
    // }
  }
}

@media (max-width: $mobile-width) {
  .advantage-card {
    padding-right: 0;
    padding-left: 16px;
  }

  .advantage-card h3 {
    margin-bottom: 10px;
    font-size: 20px;
  }
}
